import React, { PureComponent } from 'react';
import { withRouter } from 'react-router-dom'
import HeaderItem from './HeaderItem';

class Header extends PureComponent {
    componentDidMount() {
        const { location: {pathname}, history, activeMenu } = this.props;
        
        //   /main  redirect home component .
        if(pathname === '/main/setting/keywordPage') {
            history.push('/main/setting/keywordPage/checkserver');
        } else { //  path css .
            activeMenu(undefined, pathname);
        }
    }

    render() {
        const { handleActive, navList } = this.props;
        const NavLinks = navList.map(
            nav => (
                <HeaderItem
                    key={nav.idx}
                    link={nav.link}
                    name={nav.name}
                    highlighted={nav.highlighted}
                    onActive={() => handleActive(nav.idx)}
                />
            )
        )
        return (
            <div className='key-header'>
                {NavLinks}
            </div>
        );
    }
}

export default withRouter(Header);