import React, { PureComponent } from 'react';
import { ServiceItem } from '../ServiceItem';

const serviceList = [
    { name: 'checkserver', title: 'Check Server', description: '   , DNS , IP      ' },
    { name: 'sefilcare', title: 'SefilCare', description: '  ' },
    { name: 'zabbix', title: 'Zabbix', description: '  ,         ' },
    { name: 'postman', title: 'Postman', description: ' API     API    REST API  ' },
];

const style = {
    width: '95%',
    height: '80%',
    margin: '0 auto',
    marginTop: '20px',
};

class ServiceList extends PureComponent {
    render() {
        const { handleChange, zabbix, postman, sefilcare, checkserver } = this.props;
        const serviceComponents = serviceList.map(
            (service, idx) => (
                <ServiceItem
                    key={idx}
                    title={service.title}
                    name={service.name}
                    checked={
                        service.name === 'checkserver' ? checkserver :
                        service.name === 'sefilcare' ? sefilcare :
                        service.name === 'zabbix' ? zabbix :
                        service.name === 'postman' ? postman : false
                    }
                    desc={service.description}
                    onChange={handleChange}
                />
            )
        );

        return (
            <div style={style}>
                {serviceComponents}
            </div>
        );
    }
}

export default ServiceList;